.ayah-text {
	position: relative;
}

.ayah-text:focus {
	border-radius: 15px;
	background-color: antiquewhite;
	outline: 2px #a07850 solid;
}

.ayah-text:focus > span {
	display: flex;
}

.ayah-text > span {
	height: 37px;
	top: -44px;
	display: none;
	position: absolute;
	right: -2px;
	border: 2px #a07850 solid;
	border-radius: 15px;
	padding: 10px 8px;
	background-color: antiquewhite;
	z-index: 10;
}

.ayah-text > span > span {
	transform: translateY(-25px);
	padding: 0;
	border: none;
	background-color: transparent;
	cursor: pointer;
}

.ayah-img {
	position: relative;
	display: inline-block;
	transform: translateY(12px);
	margin: 0 10px;
	user-select: none;
	width: 5vw;
	max-width: max-content;
	min-width: 30px;
}

.ayah-img span {
	position: absolute;
	width: 100%;
	z-index: 1;
	text-align: center;
	line-height: 1.82rem;
}

