@font-face {
	font-family: "nabi";
	src: local("nabi"), url(./fonts/nabi.ttf) format("truetype");
}

html,
body,
#root {
	min-height: 100% !important;
	height: max-content;
	background-color: #faf1e4;
}

#root {
	display: flex;
	justify-content: center;
	align-items: center;
}

* {
	box-sizing: border-box;
	margin: 0;
	padding: 0;
	font-family: nabi;
	font-size: 35px;
	text-align: right;
}
