.app {
	position: relative;
	min-height: 100%;
	height: max-content;
	display: flex;
	flex-direction: column;
	align-items: center;
	background-color: inherit;
	padding: 30px 60px;
}

.app img {
	max-width: 100%;
	width: max-content;
	user-select: none;
	pointer-events: none;
}

.app button {
	padding: 0;
	background: transparent;
	border: 0;
}

.app svg {
	color: #222;
	z-index: 100;
	aspect-ratio: 1/1;
	width: 46px;
}

.app > div {
	display: flex;
	justify-content: center;
	align-items: center;
	justify-content: center;
}

.app > div > p {
	margin-top: 0.1rem;
	position: absolute;
	font-size: clamp(5px, 4vw, 1.1rem);
	text-align: center;
}

.app > div > label {
	border: 1px solid #ccc;
	display: inline-block;
	padding: 6px 12px;
	cursor: pointer;
	left: 80px;
	line-height: 0;
	border: none;
}

.app > p {
	max-width: 900px;
	font-size: clamp(0.7rem, 4vw, 1.1rem);
	direction: rtl;
	text-align: justify;
	line-height: clamp(5px, 2rem, 5rem);
}

.app input[type="file"] {
	display: none;
}

.app:hover label {
	display: block;
}

.app:hover .svg-span {
	position: absolute;
	display: inline;
	transform: translateY(10px);
}

.app:hover .icons {
	display: flex;
}

.svg-span {
	display: none;
}

.svg-span svg {
	transform: translateY(9px);
	margin-left: 4px;
}

.second-line {
	position: relative;
	width: 100%;
	margin-bottom: 20px;
}

.icons {
	display: none;
	position: absolute;
	left: 15px;
	top: 15px;
	gap: 15px;
}
